import * as core from '@actions/core';
import createCommit from './createCommit';
import collectStats from './collectStats';
import generateSVG from './generateSvg';
import { checkDirAndWriteFile } from './utils';
try {
    const ghTokenStats = core.getInput('gh_token_stats');
    const ghTokenCommits = core.getInput('gh_token_commits');
    const badgePath = core.getInput('badge_path');
    const username = core.getInput('username');
    const header = core.getInput('header');
    const about = core.getInput('about');
    const theme = core.getInput('theme');
    const excludeRepos = core.getInput('exclude_repos').split(',');
    const excludeReposOverride = core.getInput('exclude_repos_override').split(',');
    const commitMessage = core.getInput('commit_message');
    const commit = core.getBooleanInput('commit');
    const stats = await collectStats({ ghToken: ghTokenStats, username, excludeRepos, excludeReposOverride });
    // eslint-disable-next-line no-console
    console.log('Including stargazer stats from:');
    // eslint-disable-next-line no-console
    stats.stargazerDetails.forEach(repo => { console.log(`${repo.owner.login}/${repo.name}`); });
    const svgContent = await generateSVG({
        theme,
        header,
        about,
        stats,
        username
    });
    if (commit) {
        const commitToken = ghTokenCommits === '' ? ghTokenStats : ghTokenCommits;
        const c = {
            ghToken: commitToken,
            svgContent,
            badgePath,
            commitMessage
        };
        await createCommit(c);
    }
    checkDirAndWriteFile(badgePath, svgContent);
    core.setOutput('badgePath', badgePath);
}
catch (error) {
    if (error instanceof Error)
        core.setFailed(error.message);
}
